import { Component, OnInit } from '@angular/core';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { LoadingController,AlertController,ModalController,NavController, Platform } from '@ionic/angular';

import { AccountService } from '../../services/account/account.service';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';


@Component({
    selector: 'app-settings',
    templateUrl: './settings.page.html',
    styleUrls: ['./settings.page.scss'],
})
export class SettingsPage implements OnInit {

    public user:any;
    
    constructor(public plt: Platform, private accountService: AccountService, 
        private alertCtrl: AlertController, private iab: InAppBrowser) {
        
        this.user = {};
        this.accountService.getUserData().subscribe((value) => {
            this.user = value;
            
        });
    }

    ngOnInit() {
    }
    public openRate(){
        
        if (this.plt.is("ios")){
            this.iab.create("https://apps.apple.com/us/app/", '_system');
        }
        else{
            this.iab.create("https://play.google.com/store/apps/details?id=com.healthandsafetystories.app", '_system');
        }
        
        
    }
    public openFollow(){
        let link = 'https://www.facebook.com/Whanauora-599348520177935/';
        this.iab.create(link, '_system');        
    }
    
   
    
    public openEmail(){
        this.iab.create("mailto:tekotukubusiness@gmail.com", '_system');
    }
    
    
    public openDisclaimer(){
        let alert = this.alertCtrl.create({
            header: 'Disclaimer',
            message: 'The publisher would like to inform the reader that the safety information contained in the Te KÅtuku Apps is for education purposes only and intended for children who live in New Zealand. Please consult a health professional to see if the safety information in these apps are correct for you and your child.',
            buttons: [
                {
                    text: 'Close',
                    role: 'cancel',
                    handler: data => {
                    }
                }    
            ]
        }).then((el) => {
            el.present();
        });         
    }

    
    public openChangePassword(){
        let alert = this.alertCtrl.create({
            header: 'Change Password',
            inputs: [           
                {
                    name: 'password',
                    placeholder: 'Password',
                    type: 'password'
                },
                {
                    name: 'confirmPassword',
                    placeholder: 'Confirm Password',
                    type: 'password'
                }
            ],
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: data => {
                    }
                },
                {
                    text: 'Change',
                    handler: data => {
                        
                        if (data.password && data.confirmPassword && data.password === data.confirmPassword){

                            
                            this.accountService.changePassword(this.user.email, data.password, data.confirmPassword).then(() => {
                                let alert = this.alertCtrl.create({
                                    header: 'Success',
                                    message: 'Your password has been updated',
                                    buttons: [
                                        {
                                            text: 'Dismiss',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        }                              
                                    ]           
                                }).then((el) => {
                                    el.present();
                                });                                 
                            }).catch(() => {
                                let alert = this.alertCtrl.create({
                                    header: 'Error',
                                    message: 'There was an error while changing your password.',
                                    buttons: [
                                        {
                                            text: 'Cancel',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        },
                                         {
                                            text: 'Try Again',
                                            handler: data => {
                                                setTimeout(() => {this.openChangePassword()},200);
                                            }
                                        }                               
                                    ]            
                                }).then((el) => {
                                    el.present();
                                });                                
                            });
                        }
                        
                        else{
                            let alert = this.alertCtrl.create({
                                header: 'Error',
                                message: 'Passwords do not match',
                                buttons: [
                                    {
                                        text: 'Cancel',
                                        role: 'cancel',
                                        handler: data => {
                                        }
                                    },
                                     {
                                        text: 'Try Again',
                                        handler: data => {
                                            setTimeout(() => {this.openChangePassword()},200);
                                        }
                                    }                               
                                ]            
                            }).then((el) => {
                                    el.present();
                                });                              
                        }
    
                    }
                }
                
            ]
        }).then((el) => {
            el.present();
        });          
    }
    
    
    public openChangeEmail(){
        let alert = this.alertCtrl.create({
            header: 'Change Email',
            message: 'Your current email is: ' + this.user.email,
            inputs: [           
                {
                    name: 'email',
                    placeholder: 'New Email',
                    type: 'email'
                },
                {
                    name: 'confirmEmail',
                    placeholder: 'Confirm Email',
                    type: 'email'
                }
            ],
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: data => {
                    }
                },
                {
                    text: 'Change',
                    handler: data => {
                        
                        if (data.email && data.confirmEmail && data.email === data.confirmEmail){

                            
                            this.accountService.changeEmail(data.email).then(() => {
                                let alert = this.alertCtrl.create({
                                    header: 'Success',
                                    message: 'Your email has been updated',
                                    buttons: [
                                        {
                                            text: 'Dismiss',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        }                              
                                    ]           
                                }).then((el) => {
                                    el.present();
                                });    
                                
                                this.accountService.retreiveUserData().then((data) => {
                                    this.user = data;
                                })
                                
                                                             
                            }).catch(() => {
                                let alert = this.alertCtrl.create({
                                    header: 'Error',
                                    message: 'There was an error while changing your email.',
                                    buttons: [
                                        {
                                            text: 'Cancel',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        },
                                         {
                                            text: 'Try Again',
                                            handler: data => {
                                                setTimeout(() => {this.openChangeEmail()},200);
                                            }
                                        }                               
                                    ]            
                                }).then((el) => {
                                    el.present();
                                });                                
                            });
                        }
                        
                        else{
                            let alert = this.alertCtrl.create({
                                header: 'Error',
                                message: 'Emails do not match',
                                buttons: [
                                    {
                                        text: 'Cancel',
                                        role: 'cancel',
                                        handler: data => {
                                        }
                                    },
                                     {
                                        text: 'Try Again',
                                        handler: data => {
                                            setTimeout(() => {this.openChangeEmail()},200);
                                        }
                                    }                               
                                ]            
                            }).then((el) => {
                                    el.present();
                                });                              
                        }
    
                    }
                }
                
            ]
        }).then((el) => {
            el.present();
        });          
    }
    
    
    public openSwitchAccount(){
        let alert = this.alertCtrl.create({
            header: 'Swtich Accounts',
            inputs: [           
                {
                    name: 'email',
                    placeholder: 'Email',
                    type: 'email'
                },
                {
                    name: 'password',
                    placeholder: 'Password',
                    type: 'password'
                }
            ],
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: data => {
                    }
                },
                {
                    text: 'Login',
                    handler: data => {
                        
                        if (data.email && data.password){

                         
                            this.accountService.login({email:this.user.email, password:data.password}).then(() => {
                                let alert = this.alertCtrl.create({
                                    header: 'Success',
                                    message: 'You have switched accounts.',
                                    buttons: [
                                        {
                                            text: 'Dismiss',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        }                              
                                    ]           
                                }).then((el) => {
                                    el.present();
                                });                                 
                            }).catch(() => {
                                let alert = this.alertCtrl.create({
                                    header: 'Error',
                                    message: 'There was an error while logging in.',
                                    buttons: [
                                        {
                                            text: 'Cancel',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        },
                                         {
                                            text: 'Try Again',
                                            handler: data => {
                                                setTimeout(() => {this.openSwitchAccount()},200);
                                            }
                                        }                               
                                    ]            
                                }).then((el) => {
                                    el.present();
                                });                                
                            });
                        }
                        
                        else{
                            let alert = this.alertCtrl.create({
                                header: 'Error',
                                message: 'Emails do not match',
                                buttons: [
                                    {
                                        text: 'Cancel',
                                        role: 'cancel',
                                        handler: data => {
                                        }
                                    },
                                     {
                                        text: 'Try Again',
                                        handler: data => {
                                            setTimeout(() => {this.openSwitchAccount()},200);
                                        }
                                    }                               
                                ]            
                            }).then((el) => {
                                    el.present();
                                });                              
                        }
    
                    }
                }
                
            ]
        }).then((el) => {
            el.present();
        });          
    }
    
    
            
    public logout(){
        this.accountService.logout();
      
    } 
}
